<template>
    <div class="p-list bg">
        <ul>
            <li :class="{'select': selNum == idx}" v-for="(list, idx) in listData" @click="select(list, idx)">{{list.name}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'p-list',
        props: {
            listData: Array
        },
        data () {
            return {
                selNum: 0
            };
        },
        methods: {
            select (item, idx) {
                this.selNum = idx;
                this.$emit('handleSel', item);
            }
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "../scss/variables";

    .p-list {
        width: 100%;
        ul {
            padding-left: pxtorem(16px);
            li {
                line-height: pxtorem(54px);
                font-size: pxtorem(16px);
                color: #222;
                border-bottom: 1px solid #e1e1e1;
            }
            li:last-child {
                border-bottom: none;
            }
            .select {
                background: url("../assets/publish/radio.png") right pxtorem(16px) center no-repeat;
                background-size: pxtorem(14px) pxtorem(14px);
            }
        }
    }

</style>
