<template>
    <div class="needs">
        <p-list :listData="lists" @handleSel="select"></p-list>
    </div>
</template>

<script>
    import pList from '../../components/p-list.vue';
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';

    export default {
        name: 'needs',
        components: { pList },
        data () {
            return {
                lists: [],
                selNum: 0
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.needs'));
            this.getType();
        },
        methods: {
            getType () {
                let params = {},
                    self = this;
                params.type = 2;
                publicPost(getApiUrl('getTypes'), params).then((res) => {
                    self.lists = res;
                }).catch((e) => {
                    self.$toast.center(e.msg);
                });
            },
            select (item) {
                this.$root.needs = item;
                this.$router.go(-1);
            }
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "../../scss/variables";

    .needs {
        width: 100%;
        background: #f2f2f4;
    }

</style>
